import React, { useCallback, useState } from 'react';
import ImageViewer from 'react-simple-image-viewer';

import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Navigation, Pagination } from 'swiper';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

import './Item.scss';

import { IProject } from './App';

SwiperCore.use([Navigation, Pagination]);

interface ComponentProps {
  data: IProject;
}

const Item: React.FC<ComponentProps> = props => {
  const data = props.data;

  const [currentImage, setCurrentImage] = useState(0);
  const [isViewerOpen, setIsViewerOpen] = useState(false);

  const openImageViewer = useCallback((index: number) => {
    setCurrentImage(index);
    setIsViewerOpen(true);
  }, []);

  const closeImageViewer = () => {
    setCurrentImage(0);
    setIsViewerOpen(false);
  };

  return (
    <div className={'item-container'}>
      <div className={'item-title-container'}>
        <div className={'item-title-dot'} />
        <p className={'item-title'}>{data.title}</p>
        {/* <img className={'item-title-image'} alt={'Logo'} /> */}
      </div>

      {data.sceenshotList && (
        <Swiper
          direction={'horizontal'}
          slidesPerView={1.5}
          spaceBetween={0}
          mousewheel={true}
          pagination={{
            clickable: true,
          }}
          modules={[Pagination]}
          scrollbar={{ draggable: true }}
          breakpoints={{
            720: {
              slidesPerView: 2.5,
            },
            1080: {
              slidesPerView: 3.5,
            },
            1440: {
              slidesPerView: 4.5,
            },
            1800: {
              slidesPerView: 5.5,
            },
            2160: {
              slidesPerView: 6.5,
            },
          }}>
          {data.sceenshotList?.map((slideItem, slideIndex) => {
            return (
              <SwiperSlide key={slideIndex}>
                <img
                  src={slideItem}
                  onClick={() => openImageViewer(slideIndex)}
                  alt="Screenshot"
                />
              </SwiperSlide>
            );
          })}
        </Swiper>
      )}

      {isViewerOpen && data.sceenshotList && (
        <ImageViewer
          src={data.sceenshotList}
          currentIndex={currentImage}
          disableScroll={false}
          closeOnClickOutside={true}
          onClose={closeImageViewer}
        />
      )}

      <div className={'item-desc-container'}>
        <p className={'item-desc-title'}>{data.descTitle}</p>
        <p className={'item-desc'}>
          {data.descList.map((descItem, descIndex) => {
            return (
              <>
                {descItem}
                <br />
              </>
            );
          })}
        </p>

        {data.playStoreUrl && (
          <p className={'item-desc'}>
            {'- Google Play Store'}
            <br />
            <a
              className={'item-link'}
              href={data.playStoreUrl}
              target={'_blank'}>
              {data.playStoreUrl}
            </a>
          </p>
        )}

        {data.appStoreUrl && (
          <p className={'item-desc'}>
            {'- AppStore'}
            <br />
            <a
              className={'item-link'}
              href={data.appStoreUrl}
              target={'_blank'}>
              {data.appStoreUrl}
            </a>
          </p>
        )}
      </div>
    </div>
  );
};

export default Item;
